// Minimalist components

// Buttons
.btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  text-decoration: none;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid var(--accent);
  background: var(--accent);
  color: var(--background);
  transition: background-color 0.2s ease;
  
  &:hover {
    background: #0056b3;
    text-decoration: none;
  }
  
  &--secondary {
    background: var(--background);
    color: var(--accent);
    
    &:hover {
      background: #f8f9fa;
    }
  }
  
  &--small {
    padding: 0.25rem 0.5rem;
    font-size: 0.8rem;
  }
}

// Event Cards
.event-card {
  display: flex;
  background: var(--background);
  border: 1px solid var(--border);
  margin-bottom: 1rem;
  border-radius: 4px;
  overflow: hidden;
  
  &__date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: var(--accent);
    color: var(--background);
    padding: 1rem;
    min-width: 80px;
    text-align: center;
    
    .date__month {
      font-size: 0.8rem;
      font-weight: 600;
    }
    
    .date__day {
      font-size: 1.5rem;
      font-weight: 700;
      line-height: 1;
    }
  }
  
  &__content {
    flex: 1;
    padding: 1rem;
  }
  
  &__venue {
    font-size: 1.1rem;
    font-weight: 600;
    margin: 0 0 0.5rem 0;
    color: var(--text);
  }
  
  &__truck {
    color: var(--text-light);
    font-size: 0.9rem;
    margin: 0 0 0.5rem 0;
  }
  
  &__time {
    color: var(--text-light);
    font-size: 0.9rem;
    margin: 0 0 0.5rem 0;
  }
  
  &__location {
    color: var(--text-light);
    font-size: 0.9rem;
    margin: 0 0 1rem 0;
  }
  
  &__actions {
    display: flex;
    gap: 0.5rem;
    margin-top: 1rem;
  }
  
  &__link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
  }
}

// Map info windows
.map-info {
  background: var(--background);
  border: 1px solid var(--border);
  padding: 1rem;
  max-width: 250px;
  border-radius: 4px;
  
  &__title {
    font-size: 1.1rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
    color: var(--text);
  }
  
  &__venue {
    color: var(--text-light);
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }
  
  &__time {
    color: var(--text-light);
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }
  
  &__address {
    color: var(--text-light);
    font-size: 0.9rem;
    margin-bottom: 1rem;
  }
  
  &__actions {
    display: flex;
    gap: 0.5rem;
  }
}